import React, { Component } from 'react';
import io from 'socket.io-client';

const moment = require('moment');

class Jam extends Component {
    state = {
        time : new Date(),
        waktuSholat:[]
    }

    componentDidMount(){
        this.timerID = setInterval(() => this.thick(), 1000)
        if(this.state.time.toLocaleTimeString() == moment('15:49:00', 'HH:mm:ss')){
            window.alert('oke')
        }

        // const socket = io.connect('http://localhost:2020')
        // socket.on('adzan', ()=> this.popupalert('dari Adzan'))
        // socket.on('adzanIndex', ()=> this.popupalert('dari adzanIndex'))
        // socket.on('jadwalHariIni', (jadwalHariIni)=>this.setState({jadwalHariIni}))
        // console.log('socket io adzan is On')
    }

    popupalert = (mst) => {
        window.alert('oke '+mst)
        console.log('masuk sini-----------------------------')
    }

    componentWillUnmount(){
        clearInterval(this.timerID);
    }

    thick(){
        this.setState({
            time: new Date()
        })
    }

    renderwaktusholat = () =>{
        console.log(this.state.waktuSholat)
        let a = ''
        for (const [key, value]of Object.entries(this.state.waktuSholat)){
            a+=`<div>${value}</div>`
            
        }
        return a
    }
    render(){
        return (
            <div>
                <h1>Clock</h1>
                <h2>sekarang jam {this.state.time.toLocaleTimeString()}</h2>
                {console.log(this.state.time.toLocaleTimeString())}
                {
                    moment('16:41', 'HH:mm:ss') == this.state.time.toLocaleTimeString() ? 
                        window.alert('oke') :
                        null

                }
                <div id='oke'>
                {this.renderwaktusholat()}
                </div>

            </div>
        )
    }

}
export default Jam;