import React from 'react';


function gmod(n, m) {
  return ((n % m) + m) % m;
}

function kuwaiticalendar(adjust) {
  let today = new Date();
  let adjustmili;
  let todaymili;
  let day;
  let month;
  let year;
  let m;
  let y;
  let a;
  let b;
  let wd;

  if (adjust) {
    adjustmili = 1000 * 60 * 60 * 24 * adjust;
    todaymili = today.getTime() + adjustmili;
    today = new Date(todaymili);
  }
  day = today.getDate();
  month = today.getMonth();
  year = today.getFullYear();
  m = month + 1;
  y = year;
  if (m < 3) {
    y -= 1;
    m += 12;
  }

  a = Math.floor(y / 100.0);
  b = 2 - a + Math.floor(a / 4.0);
  if (y < 1583) b = 0;
  if (y === 1582) {
    if (m > 10) b = -10;
    if (m === 10) {
      b = 0;
      if (day > 4) b = -10;
    }
  }

  const jd = Math.floor(365.25 * (y + 4716)) + Math.floor(30.6001 * (m + 1)) + day + b - 1524;

  b = 0;
  if (jd > 2299160) {
    a = Math.floor((jd - 1867216.25) / 36524.25);
    b = 1 + a - Math.floor(a / 4.0);
  }
  const bb = jd + b + 1524;
  let cc = Math.floor((bb - 122.1) / 365.25);
  const dd = Math.floor(365.25 * cc);
  const ee = Math.floor((bb - dd) / 30.6001);
  day = (bb - dd) - Math.floor(30.6001 * ee);
  month = ee - 1;
  if (ee > 13) {
    cc += 1;
    month = ee - 13;
  }
  year = cc - 4716;

  if (adjust) {
    wd = gmod(jd + 1 - adjust, 7) + 1;
  } else {
    wd = gmod(jd + 1, 7) + 1;
  }

  const iyear = 10631.0 / 30.0;
  const epochastro = 1948084;
  const epochcivil = 1948085;

  const shift1 = 8.01 / 60.0;

  let z = jd - epochastro;
  const cyc = Math.floor(z / 10631.0);
  z -= 10631 * cyc;
  const j = Math.floor((z - shift1) / iyear);
  const iy = 30 * cyc + j;
  z -= Math.floor(j * iyear + shift1);
  let im = Math.floor((z + 28.5001) / 29.5);
  if (im === 13) im = 12;
  const id = z - Math.floor(29.5001 * im - 29);

  const myRes = new Array(8);

  myRes[0] = day; // calculated day (CE)
  myRes[1] = month - 1; // calculated month (CE)
  myRes[2] = year; // calculated year (CE)
  myRes[3] = jd - 1; // julian day number
  myRes[4] = wd - 1; // weekday number
  myRes[5] = id; // islamic date
  myRes[6] = im - 1; // islamic month
  myRes[7] = iy; // islamic year

  return myRes;
}
export default function writeIslamicDate(adjustment) {
  const wdNames = new Array('Ahad', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jum\'at', 'Sabtu');
  const iMonthNames = new Array('Muharram', 'Safar', "Rabi'ul Awwal", "Rabi'ul Akhir",
    'Jumadil Ula', 'Jumadil Akhira', 'Rajab', "Sya'ban",
    'Ramadan', 'Syawal', "Dzulqa'ada", 'DzulHijja');
  const iDate = kuwaiticalendar(adjustment);
  const outputIslamicDate = `${iDate[5]} ${iMonthNames[iDate[6]]} ${iDate[7]}`;
  return outputIslamicDate;
}

export function exposeDay(adjustment) {
  const wdNames = new Array('Ahad', 'Senin', 'Selasa', 'Rabu', 'Kamis', 'Jum\'at', 'Sabtu');
  const iDates = kuwaiticalendar(adjustment);
  const outDay = `${wdNames[iDates[4]]}`;
  return outDay;
}

